.bonuses {
  --v-bonuses-container-gap: 2rem;
}
.bonuses__container {
  display: flex;
  flex-direction: column;
  gap: var(--v-bonuses-container-gap);
}
@media (max-width: 600px) {
  .bonuses {
    --v-bonuses-container-gap: 1.5rem;
  }
}
@media (max-width: 575px) {
  .bonuses {
    --v-bonuses-container-gap: 1rem;
  }
}

.bonus {
  --v-bonus-padding: 1.3333rem;
  --v-bonus-description-gap: 2rem;
  --v-bonus-group-gap: 1rem;
  --v-bonus-group-padding-left: 1.3333rem;
  --v-bonus-group-padding-y: 1.3333rem;
  --v-bonus-description-padding-y: 1.3333rem;
  display: flex;
  background-color: var(--v-black);
  padding: 0 var(--v-bonus-padding);
}
.bonus__group {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  gap: var(--v-bonus-group-gap);
  padding: var(--v-bonus-group-padding-y) 0;
}
.bonus__group--border {
  border-left: 1px solid var(--v-gray-700);
  padding-left: var(--v-bonus-group-padding-left);
}
@media (max-width: 600px) {
  .bonus__group--border {
    border-left: none;
    border-top: 1px solid var(--v-gray-700);
    padding-left: 0;
    padding-top: 1.3333rem;
    margin-top: 1rem;
  }
}
@media (max-width: 600px) {
  .bonus__group {
    padding: 1rem 0;
  }
}
.bonus__title {
  line-height: 100%;
}
@media (max-width: 600px) {
  .bonus__title {
    font-size: 1rem !important;
  }
}
@media (max-width: 575px) {
  .bonus__title {
    font-size: 0.9rem !important;
  }
}
.bonus__amount--orange {
  color: var(--v-orange);
}
.bonus__amount--lemon {
  color: var(--v-yellow);
}
.bonus__amount--lime {
  color: var(--v-lime);
}
.bonus__description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  padding: var(--v-bonus-description-padding-y) 0;
  line-height: 130%;
  gap: var(--v-bonus-description-gap);
}
.bonus__description-title {
  margin-bottom: 0.3333rem;
}
@media (max-width: 600px) {
  .bonus__description-title {
    font-size: 1rem !important;
  }
}
.bonus__description-text {
  color: var(--v-gray-500);
}
@media (max-width: 600px) {
  .bonus__description-text br {
    display: none;
  }
}
@media (max-width: 600px) {
  .bonus__description-text {
    font-size: 0.8667rem;
  }
}
@media (max-width: 575px) {
  .bonus__description-text {
    font-size: 0.8rem;
  }
}
@media (max-width: 600px) {
  .bonus__description-request {
    font-size: 0.8667rem;
  }
}
@media (max-width: 600px) {
  .bonus__description {
    --v-bonus-description-gap: 1rem;
    --v-bonus-description-padding-y: 1rem;
  }
}
@media (max-width: 600px) {
  .bonus__btn .btn {
    width: 100%;
    font-size: 0.75rem;
    padding: 0.6rem 1rem;
  }
}
@media (max-width: 600px) {
  .bonus__text {
    font-size: 0.8rem !important;
  }
}
@media (max-width: 991px) {
  .bonus {
    --v-bonus-padding: 1rem;
  }
  .bonus .row.g-0 > [class*=col-] {
    flex: 0 0 100%;
    max-width: 100%;
    padding-bottom: 16px;
  }
}
@media (max-width: 600px) {
  .bonus {
    --v-bonus-padding: 0.75rem;
    flex-direction: column;
  }
}
@media (max-width: 575px) {
  .bonus {
    --v-bonus-padding: 0.5rem;
  }
}

.breadcrumb {
  margin-bottom: 2rem;
}
.breadcrumb__list {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin: 0;
  padding: 0;
  list-style: none;
  flex-wrap: wrap;
}
.breadcrumb__item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.breadcrumb__link {
  color: var(--v-gray-400);
  text-decoration: none;
  transition: color 0.2s ease;
}
.breadcrumb__link:hover {
  color: var(--v-white);
}
.breadcrumb__text {
  font-size: 0.9333rem;
}
@media (max-width: 600px) {
  .breadcrumb__text {
    font-size: 0.8667rem;
  }
}
.breadcrumb__icon {
  color: var(--v-gray-600);
  font-size: 0.75rem;
}
@media (max-width: 600px) {
  .breadcrumb {
    margin-bottom: 1.5rem;
  }
}

@media (max-width: 600px) {
  .container-xl .row.align-items-end {
    flex-direction: column;
    align-items: flex-start !important;
    gap: 1rem;
  }
  .container-xl .row.align-items-end .col-9,
  .container-xl .row.align-items-end .col-3 {
    flex: 0 0 100%;
    max-width: 100%;
  }
  .container-xl .row.align-items-end .text-end {
    text-align: left !important;
  }
}
@media (max-width: 600px) {
  .container-xl h1 {
    font-size: 1.75rem !important;
  }
}
@media (max-width: 575px) {
  .container-xl h1 {
    font-size: 1.5rem !important;
  }
}
@media (max-width: 600px) {
  .container-xl .more__link {
    font-size: 0.8667rem;
  }
}

@media (max-width: 600px) {
  .h-positive__row {
    gap: 1.5rem !important;
  }
}
@media (max-width: 600px) {
  .h-positive__item {
    padding: 1.5rem;
  }
}
@media (max-width: 575px) {
  .h-positive__item {
    padding: 1rem;
  }
}
@media (max-width: 575px) {
  .h-positive__item-icon {
    margin-bottom: 1rem !important;
  }
  .h-positive__item-icon img {
    max-height: 50px;
  }
  .h-positive__item-icon i {
    font-size: 2.5rem;
  }
}
@media (max-width: 600px) {
  .h-positive__item-title {
    font-size: 1rem !important;
  }
}
@media (max-width: 575px) {
  .h-positive__item-title {
    font-size: 0.95rem !important;
  }
}